<template>
<div class="perspective" @click.prevent="turn" :style="ht">
    <div class="cube" :style="stl" >
        <div class="page" :style="p1"></div>
        <div class="page" :style="p2"></div>
        <div class="page" :style="p3"></div>
        <div class="page" :style="p4"></div>
    </div>

</div>
    

    
</template>

<script>
export default {
  name: 'new',
  data() {
      return {
          stl: {
              transform:''
          },
          deg: 0,
          ht: {},
          p1: {},
          p2: {},
          p3: {},
          p4: {}
      }
  },
  methods: {
      getHeight() {
      this.ht.height = window.innerHeight+"px"
      this.p1.transform = "rotateX(-270deg) translateY("+(-window.innerHeight/2) +"px)"
      this.p2.transform = "translateZ("+(window.innerHeight/2) +"px)"
      this.p3.transform = "rotateX(-90deg) translateY("+(window.innerHeight/2) +"px)"
      this.p4.transform = "translateZ("+(-window.innerHeight/2) +"px)"
        },
      turn() {
          this.deg += 90
          this.stl.transform = "rotateX("+this.deg+"deg)"
          
      }
  },
  created() {
      this.getHeight()
  }
}
</script>

<style lang="scss" scoped>
    .perspective {
        perspective: 2500;
        perspective-origin: 50% 50%;
    }
    .cube {
        margin: 100px auto;
        position: relative;
        width: 750px;
        height: 100%;
        transform-origin: 50% 50% 0;
        transform-style: preserve-3d;
        transition: transform 0.7s ease-in;
        
    }
    .page {
        width: 750px;
        height: 100%;
        position: absolute;
    }
    .page:nth-child(1) {
        background-color: #06D6A0;    
        transform-origin: top left;
    }
    .page:nth-child(2) {
        background-color: #1B9AAA;

    }
    .page:nth-child(3) {
        background-color: #EF476F;
        transform-origin: bottom center;
    }
    .page:nth-child(4) {
        background-color: #333;          
    }
</style>



