<template>
  <div class="item">
      <div class="avater"><img :src="data.author.avater" alt="avater"></div>
      <div class="content">
          <h3 class="author">{{data.author.name}}</h3>
          <div class="main" v-if="data.text">{{data.text}}</div>
          <div class="art" v-if="data.art.title">
              <img :src="data.art.img" alt="img">
              <div class="title">{{data.art.title}}</div>
          </div>
          <div class="images" v-if="data.img[0]">
              <div class="big" v-if="sgImg"><img :src="data.img[0]" alt="img"></div>
              <div class="thumb" v-else>
                  <div v-for="item in data.img" :key="item.key" class="perImg">
                      <img :src="item" alt="">
                  </div>
              </div>
          </div>
          <div class="info">
              <div class="time">{{data.date}}</div>
              <i class="iconfont icon-pengyouquanxiaoxi-copy"></i>
          </div>
          <div class="comment">
              <div class="like">
                  <i class="iconfont icon-xin"></i>
                  <span class="pep" v-for="item in data.like" :key="item.key">{{item}}</span>
              </div>
              <div class="cmt"></div>
          </div>
      </div>
  </div>
</template>

<script>
export default {
    name: 'item',
    props: ['data'],
    data() {
        return {
            sgImg:true
        }
    },
    mounted() {
        if(this.data.img.length > 1) {
            this.sgImg = false
        }
    }
  
}
</script>

<style lang="scss" scoped>
    .item {
        position: relative;
        bottom: 90px;
        width: 100%;
        display: flex;
        align-items: flex-start;
        .avater {
            display: block;
            margin: 25px;
            img {
                width: 90px;
                height: 90px;
            }

            
        }
        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 2px;
            transform: scaleY(.5);
            background-color: #d1d1d1;
        }
        .content {
            min-height: 150px;
            margin: 20px 25px 15px 0;
            width: 615px;
            .author {
                color: #6e79a6;
                font-size: 32px;
                font-weight: normal;
                line-height: 55px;
            }
            .main {
                font-size: 32px;
                color: #333;
            }
            .info {
                padding-top: 20px;
                display: flex;
                justify-content: space-between;
                .iconfont {
                    color: #7f93ac;
                    &::before {
                        font-size: 50px;
                    } 
                }
                .time {
                    color: #828282;
                    text-shadow: 2px 2px 2px #fff;
                }
                
            }
            .art {
                background-color: #ebebeb;
                height: 110px;
                display: flex;
                align-items: center;
                margin: 20px 0;
                img {
                   height: 80%;
                   margin: 0 11px;
                }
                .title {
                    font-size: 32px;
                    margin-left: 11px;
                }
            }
            .images {
                .big {
                    margin: 20px 0;
                    img {
                        width: 70%;
                    }
                }
                .thumb {
                    padding: 20px 0;
                    display: flex;
                    flex-wrap: wrap;
                    .perImg {
                        width: 180px;
                        height: 180px;
                        overflow: hidden;
                        padding: 5px 0 0 5px;
                        img {
                            height: 100%;
                        }
                    }
                }
            }
            .comment {
                background-color: #efefef;
                font-size: 30px;
                line-height: 60px;
                .like {
                    position: relative;
                    color: #4f5f80;
                    .icon-xin{
                        display: inline-block;
                        font-size: 50px;
                    }
                    .icon-xin::before {
                        position: relative;
                        top: 8px;
                        left: 5px;
                    }
                    .pep {
                        display: inline-block;
                        margin-left: 10px;
                    }
                    &::after {
                        content: '';
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        width: 100%;
                        height: 2px;
                        background-color: #dedede;
                        transform: scaleY(.5);
                    }
                }
            }
        }
    }
</style>



