import Vue from 'vue'
import Router from 'vue-router'
import main from '@/components/main'
import newPage from '@/components/new'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'main',
      component: main
    },
    {
      path: '/new',
      name: 'new',
      component: newPage
    }
  ]
})



